import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    value: 1,
    maxValue: 100,
    speed: 250,
};

export const progressSlide = createSlice({
    name: "progress number",
    initialState,
    reducers: {
        Add: (state) => {
            if (state.value < state.maxValue) {
                state.value = state.value + 1;
            }
        },
        Reset: (state) => {
            state.value = 0;
        },
    },
});

// Action creators are generated for each case reducer function
export const { Add, Reset } = progressSlide.actions;

export default progressSlide.reducer;
