import React from "react";
import { useEffect } from "react";
import ProgressBar from "@ramonak/react-progress-bar";
import { useSelector, useDispatch } from "react-redux";
import { Add } from "../Lib/progress";
import "../CSS/loading.css";

function Loading() {
    const progress = useSelector((state) => state.progress.value);
    const progressMax = useSelector((state) => state.progress.Maxvalue);
    const progressSpeed = useSelector((state) => state.progress.speed);

    const dispatch = useDispatch();
    useEffect(() => {
        setInterval(() => dispatch(Add()), progressSpeed);
    }, []);

    return (
        <ProgressBar
            completed={progress}
            maxCompleted={progressMax}
            height={"30px"}
            width={"600px"}
            transitionTimingFunction={"ease-out"}
            className="progressBarClass"
        />
    );
}

export default Loading;
