import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    value: "Explore",
};

export const pageSlide = createSlice({
    name: "change page",
    initialState,
    reducers: {
        Explore: (state) => {
            state.value = "Explore";
        },
        Gallery: (state) => {
            state.value = "Gallery";
        },
    },
});

// Action creators are generated for each case reducer function
export const { Explore, Gallery } = pageSlide.actions;

export default pageSlide.reducer;
